export default {
	name: 'UniNoticeBar', 
	props: {
		text: {
			type: String,
			default: ''
		},
		moreText: {
			type: String,
			default: ''
		},
		backgroundColor: {
			type: String,
			default: '#fffbe8'
		},
		speed: { // 默认1s滚动100px
			type: [String, Number],
			default: 100
		},
		color: {
			type: String,
			default: '#de8c17'
		},
		single: { // 是否单行
			type: Boolean,
			default: false
		},
		scrollable: { // 是否滚动，添加后控制单行效果取消
			type: Boolean,
			default: false
		},
		showIcon: { // 是否显示左侧icon
			type: Boolean,
			default: false
		},
		showGetMore: { // 是否显示右侧查看更多
			type: Boolean,
			default: false
		},
		showClose: { // 是否显示左侧关闭按钮
			type: Boolean,
			default: false
		}
	},
	data() {
		/**
		 * TODO 兼容新旧编译器
		 * 新编译器（自定义组件模式）下必须使用固定数值，否则部分平台下会获取不到节点。
		 * 随机数值是在旧编译器下使用的，旧编译器模式已经不推荐使用，后续直接废掉随机数值的写法。
		 */
		const elId = this.__call_hook ? 'uni_notice_bar' : `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			elId: elId,
			show: true,
			animation: ''
		}
	},
	watch: {
		text(newValue, oldValue) {
			this.$nextTick(() => {
				setTimeout(this.setAnimation, 200)
			})
		}
	},






	onReady() {
		this.setAnimation()
	},

	methods: {
		clickMore() {
			this.$emit('getmore')
		},
		onClick(e) {
			let clientX = e.touches ? (e.touches[0] ? e.touches[0].clientX : e.changedTouches[0].clientX) : e.detail.clientX
			if (uni.upx2px(48) + 12 > clientX && this.showClose) {
				this.show = false
				this.$emit('close')
			}
			this.$emit('click')
		},
		setAnimation() {
			if (!this.scrollable) {
				return
			}








			uni.createSelectorQuery().in(this).select(`#${this.elId}`).boundingClientRect().exec((ret) => {
				this.animation = `notice ${ret[0].width / this.speed}s linear infinite both`
			})

		}
	}
}
